import React from 'react';
import { Row, Col, Button, Form } from 'react-bootstrap';

import classes from '../pages/artists/artist.module.css';
import globalClasses from '../constants/globalStyles.module.css';

function ArtistForm(props) {

    // const { } = props;

    return ( 
        <React.Fragment>
            <Row style={{marginBottom:20}}>
                <Col>
                    <Form.Label className={globalClasses.label}>
                        Artists name<span style={{color:'#FF4848'}}>*</span>
                    </Form.Label>
                    <Form.Control type="text" placeholder="Type here" />
                </Col>
            </Row>
            <Row style={{marginBottom:20}}>
                <Col>
                    <div className={globalClasses.flexRowSpace}>
                        <Form.Label className={globalClasses.label}>
                            Description
                        </Form.Label>
                        <div style={{fontSize:14, opacity:0.6}}>
                            0/400
                        </div>
                    </div>
                    <Form.Control as="textarea" style={{height:125}} placeholder="Type here" />
                </Col>
            </Row>
            <Row style={{marginBottom:20}}>
                <Col>
                    <Form.Label className={globalClasses.label}>
                        Artform<span style={{color:'#FF4848'}}>*</span>
                    </Form.Label>
                    <Form.Select placeholder="Select" className={globalClasses.selectInput}>
                        <option>Select</option>
                    </Form.Select>
                </Col>
            </Row>
            <Row style={{marginBottom:20}}>
                <Col>
                    <Form.Label className={globalClasses.label}>
                       Organisation
                    </Form.Label>
                    <Form.Select placeholder="Select" className={globalClasses.selectInput}>
                        <option>Select</option>
                    </Form.Select>
                </Col>
            </Row>
            <Row style={{marginBottom:20}}>
                <Col>
                    <Form.Label className={globalClasses.label}>
                        State
                    </Form.Label>
                    <Form.Select placeholder="Select" className={globalClasses.selectInput}>
                        <option>Select</option>
                    </Form.Select>
                </Col>
                <Col>
                    <Form.Label className={globalClasses.label}>
                        City/Town
                    </Form.Label>
                    <Form.Select placeholder="Select" className={globalClasses.selectInput}>
                        <option>Select</option>
                    </Form.Select>
                </Col>
            </Row>
            <Row style={{marginBottom:20}}>
                <Col style={{position:'relative'}}>
                    <Form.Label className={globalClasses.label}>
                        Phone Number
                    </Form.Label>
                    <Form.Control style={{paddingLeft:60}} type="text" placeholder="Enter 10 digit number" />
                    <div className={classes.phoneNum}>+91</div>
                </Col>
                <Col>
                    <Form.Label className={globalClasses.label}>
                        Email ID
                    </Form.Label>
                    <Form.Control type="text" placeholder="Type here" />
                </Col>
            </Row>
            <Row style={{marginBottom:20}}>
                <Col>
                    <Form.Label className={globalClasses.label}>
                        Wallet address<span style={{color:'#FF4848'}}>*</span>
                    </Form.Label>
                    <Form.Control type="text" placeholder="Enter URL here" />
                </Col>
            </Row>
            <Row style={{marginBottom:20}}>
                <Col>
                    <Form.Label className={globalClasses.label}>
                        Artist website
                    </Form.Label>
                    <Form.Control type="text" placeholder="Enter URL here" />
                </Col>
            </Row>
            <Row style={{marginBottom:20}}>
                <Col>
                    <Form.Label className={globalClasses.label}>
                        Facebook
                    </Form.Label>
                    <Form.Control type="text" placeholder="Enter URL here" />
                </Col>
                <Col>
                    <Form.Label className={globalClasses.label}>
                        Instagram
                    </Form.Label>
                    <Form.Control type="text" placeholder="Enter URL here" />
                </Col>
            </Row>
        </React.Fragment>
    )
}

export default ArtistForm;
