import React from 'react';
import { Row, Col, Button, Form } from 'react-bootstrap';
import Select from 'react-select';

import classes from '../pages/artists/artist.module.css';
import globalClasses from '../constants/globalStyles.module.css';
import { Plus, Minus } from './svgComponents';

export function ArtistForm(props) {

    // const { } = props;

    const options = [
        { value: 'chocolate', label: 'Chocolate' },
        { value: 'strawberry', label: 'Strawberry' },
        { value: 'vanilla', label: 'Vanilla' }
      ]

    return ( 
        <div>
            <div className={globalClasses.containerCard}>
                <Row style={{marginBottom:20}}>
                    <Col>
                        <Form.Label className={globalClasses.label}>
                            Name<span style={{color:'#FF4848'}}>*</span>
                        </Form.Label>
                        <Form.Control type="text" placeholder="Type here" />
                    </Col>
                </Row>
                <Row style={{marginBottom:20}}>
                    <Col>
                        <div className={globalClasses.flexRowSpace}>
                            <Form.Label className={globalClasses.label}>
                                Description
                            </Form.Label>
                            <div style={{fontSize:14, opacity:0.6}}>
                                0/400
                            </div>
                        </div>
                        <Form.Control as="textarea" style={{height:125}} placeholder="Type here" />
                    </Col>
                </Row>
                <Row style={{marginBottom:20}}>
                    <Col>
                        <Form.Label className={globalClasses.label}>
                            Material/Medium used
                        </Form.Label>
                        <Form.Control type="text" placeholder="Type here" />
                    </Col>
                </Row>
                <Row style={{marginBottom:20}}>
                    <Col>
                        <Form.Label style={{opacity:0.6}} className={globalClasses.label}>
                            Artist*
                        </Form.Label>
                        <Select
                            styles={{valueContainer: () => ({height:42, paddingLeft:10, border:'none'}), indicatorSeparator: () => ({width:0})}}
                            className="basic-single"
                            classNamePrefix="select"
                            defaultValue={options[0]}
                            isDisabled={true}
                            isClearable={false}
                            // isRtl={false}
                            isSearchable={false}
                            name="color"
                            options={options}
                        />
                    </Col>
                    <Col>
                        <Form.Label className={globalClasses.label}>
                            Quantity (Max 10)<span style={{color:'#FF4848'}}>*</span>
                        </Form.Label>
                        <div className={globalClasses.flexRow}>
                            <Minus size={25}/>
                            <Form.Control maxLength={2} style={{width:45, margin:'0px 10px'}} type="text"/>
                            <Plus size={25}/>
                        </div>
                    </Col>
                </Row>
            </div>
            <div className={globalClasses.containerCard} style={{marginTop:15}}>
                <div style={{background: 'rgba(230, 238, 255, 0.8)', padding:'7px 15px', borderRadius:6, fontWeight:400, fontSize:18, marginBottom:15}}>
                    <Form.Check type="checkbox" label="List in marketplace" />
                </div>
                <Row style={{marginBottom:20}}>
                    <Col lg={6} style={{marginBottom:20}}>
                        <Form.Label className={globalClasses.label}>
                            Material/Medium used
                        </Form.Label>
                        <Form.Control type="text" placeholder="Type here" />
                    </Col>
                    <Col lg={6} style={{marginBottom:20}}>
                        <Form.Label className={globalClasses.label}>
                            Material/Medium used
                        </Form.Label>
                        <Form.Control type="text" placeholder="Type here" />
                    </Col>
                    <Col lg={6} style={{marginBottom:20}}>
                        <Form.Label className={globalClasses.label}>
                            Material/Medium used
                        </Form.Label>
                        <Form.Control type="text" placeholder="Type here" />
                    </Col>
                </Row>
            </div>
        </div>
    )
}

export default ArtistForm;
