import React, { Component } from 'react';
import { Row, Col, Button, Form } from 'react-bootstrap';
import { motion } from "framer-motion";

import { _getPresignedUrl, _uploadFile } from '../../services/axios/api';
import ArtistForm from '../../components/ArtistForm';
import Modal from '../../components/uiElements/Modal';
import { functions, animateConfigs } from '../../constants';
import { EditIcon, Profile } from '../../components/svgComponents';
import classes from './artist.module.css';
import globalClasses from '../../constants/globalStyles.module.css';

export default class CreateArtist extends Component {

    constructor(props) {
        super(props);
        this.state = {
            showModal: false,
            artistImage: null
        }
    }
      
    handleImage = async (e) => {
        const res = await functions.readImage(e);
        this.setState({artistImage:res[0]});
    }

    handleDeleteImage = () => {

    }

    buildImgTag = () => {
        return <div style={{display:'flex', marginRight:10}}>
            <img height='180px' width= '180px' style={{borderRadius:100}} src={this.state.artistImage} alt="upload"/>
            {/* <p onClick={this.handleDeleteImage()} style={{marginLeft: -13, marginTop: -10, marginRight:10}}><CancelIcon/></p> */}
        </div>
    } 
    
    handleArtistCreation = async () => {
        const presignedRes = await _getPresignedUrl({"module": "artist", "totalFiles": 1});
        const uploadRes = await _uploadFile({"module": "artist", "totalFiles": 1});
        console.log(presignedRes, 'presignedRes');
        // const data = {
        //     "image": "artist/b9fc85b5-0583-4318-8b0a-b8258d3daa23",
        //     "name": "abhishek",
        //     "description": "sample description",
        //     "artform": "611a33944d44577408c239ff",
        //     "organization": "611a34cc4d44577408c23a07",
        //     "state": "tamil nadu",
        //     "city": "chennai",
        //     "phone": "9876543210",
        //     "email": "abhishek@gmail.com",
        //     "wallet": "string value",
        //     "website": "abhishek.com",
        //     "facebook": "facebook.com",
        //     "instagram": "instagram.com",
        //     "custom": [
        //         {
        //             "type": 0,
        //             "name": "history",
        //             "value": "This artist was born in 1995"
        //         }
        //     ]
        // }
    }



    render() {

        const CustomInput = () => {
            return <Row style={{marginBottom:20}}>
                <Col lg={12}>
                    <Form.Label className={globalClasses.label}>
                        Artists name<span style={{color:'#FF4848'}}>*</span>
                    </Form.Label>
                    <Form.Control type="text" placeholder="Type here" />
                </Col>
            </Row>
        }

        return (
            <motion.div variants={animateConfigs.fadeInOut} initial="hidden" animate="show">
                <div className={globalClasses.flexRowSpace}> 
                    <div style={{fontWeight:600, fontSize:22}}>
                        New artist
                    </div>
                    <div>
                        <Button variant="outline-primary" style={{width:125}} className={globalClasses.outlinePrimaryBtn}>Cancel</Button>
                        <Button onClick={this.handleArtistCreation} style={{width:125, height:40, marginLeft:10, opacity:0.5}} className={globalClasses.primaryBtn}>Create Artist</Button>
                    </div>
                </div>
                <div style={{marginTop:25, display:'flex', justifyContent:'space-between'}}>
                    <div className={globalClasses.containerCard} style={{width:'28%', textAlign:'center', padding: "50px", height: "fit-content"}}>
                        <label htmlFor="addImg" style={{position:'relative', cursor:'pointer'}}>
                            <input onChange={(e) => this.handleImage(e)} id="addImg" hidden type="file" name="Pick an Image" accept="image/x-png,image/gif,image/jpeg"/>
                            {this.state.artistImage ?
                                this.buildImgTag() :
                            <Profile/>}
                            <div className={classes.editIcon}>
                                <EditIcon size={20}/>
                            </div>
                        </label>
                        <div style={{fontSize:16, fontWeight:600, opacity:0.7, marginTop:15}}>Click to upload photo</div>
                    </div>
                    <div className={globalClasses.containerCard} style={{width:'70%'}}>
                        <ArtistForm/>
                        <Button onClick={() => this.setState({showModal:!this.state.showModal})} variant="outline-primary" style={{width:'100%', marginTop:20}} className={globalClasses.outlinePrimaryBtn}>
                            Add custom fields
                        </Button>
                    </div>
                </div>
                <Modal
                    title={'Add custom field'} 
                    show={this.state.showModal}
                    body={<CustomInput/>}
                    onHide={() => this.setState({showModal:false})}
                />
            </motion.div>
        )
    }
} 
