import axios from 'axios';
import configs from '../../configs';

axios.defaults.baseURL = configs.baseURL;

// GET

export const _getAllArtforms = () => axios.get('/artform/all');


// POST  

export const _getPresignedUrl = (data) => axios.post('/file/upload', data);

export const _postArtforms = (data) => axios.post('/artform/add', data);


// PUT 

export const _uploadFile = (presignedUrl) => axios.put(presignedUrl);


// PATCH

export const _updateArtform = (id, data) => axios.patch(`/artform/update?id=${id}`, data);


// DELETE 

export const _deleteArtform = (id) => axios.delete(`/artform/delete?id=${id}`);


