import React from 'react';
import { Row, Col, Button } from 'react-bootstrap';
import { motion } from "framer-motion";

import ArtistCard from '../../components/ArtistCard';
import { animateConfigs } from '../../constants';
import { colors } from '../../constants';
import { ChevronLeft, Instagram, Facebook, Globe, Wallet } from '../../components/svgComponents';
import classes from './artist.module.css';
import globalClasses from '../../constants/globalStyles.module.css';

const DUMMY_ARTS = [
    {
        image:"https://www.biography.com/.image/t_share/MTc5OTk1MzU0Nzk4NzYxMDgy/gettyimages-858141682.jpg",
        name:"Jackie chan",
        place:"China",
        date:"13/12/2021"
    },
    {
        image:"https://www.biography.com/.image/t_share/MTc5OTk1MzU0Nzk4NzYxMDgy/gettyimages-858141682.jpg",
        name:"Jackie chan",
        place:"China",
        date:"13/12/2021"
    },
    {
        image:"https://www.biography.com/.image/t_share/MTc5OTk1MzU0Nzk4NzYxMDgy/gettyimages-858141682.jpg",
        name:"Jackie chan",
        place:"China",
        date:"13/12/2021"
    },
    {
        image:"https://www.biography.com/.image/t_share/MTc5OTk1MzU0Nzk4NzYxMDgy/gettyimages-858141682.jpg",
        name:"Jackie chan",
        place:"China",
        date:"13/12/2021"
    },
    {
        image:"https://www.biography.com/.image/t_share/MTc5OTk1MzU0Nzk4NzYxMDgy/gettyimages-858141682.jpg",
        name:"Jackie chan",
        place:"China",
        date:"13/12/2021"
    },
];


export default function ViewArtist({ history }) {

    const renderArtwork = () => {
        return DUMMY_ARTS.map((art, index) => {
            return <Col lg={6}>
                <ArtistCard
                    key={index}
                    image={art.image}
                    name={art.name}
                    date={art.date}
                    status={'Sold'}
                    availableNo={null}
                    bottomLeftTitle={
                        <div style={{fontSize:14, display:'flex', alignItems:'center', marginLeft:-5, marginRight:15}}>
                            <div style={{marginTop:-3}}><Wallet size={15} color={colors.inActiveIcon}/></div>
                            <div style={{opacity:0.8, marginLeft:2}}>{'12.05 N'}</div>
                        </div>}
                    onClick={() => history.push('/viewartist/1235678')}
                />
            </Col>
        }) 
    } 

    const emptyState = () => {
        return <div style={{textAlign:'center', marginTop:'25%'}}>
            <div style={{fontSize:26, opacity:0.7}}>No NFTs added by this artist</div>
            <Button onClick={() => history.push('/createartistnft')} className={globalClasses.outlinePrimaryBtn} style={{width:100, marginTop:10}}>
                New NFT
            </Button>
        </div>
    }
    
    return (
        <motion.div variants={animateConfigs.fadeInOut} initial="hidden" animate="show">
            <header className={globalClasses.flexRowSpace} style={{marginBottom: '40px'}}>
                <div onClick={() => history.goBack()} className={globalClasses.flexRow} style={{cursor:'pointer'}}>
                    <ChevronLeft size={16}/>
                    <span style={{fontWeight:600, fontSize:22, marginLeft:17, marginTop:-2}}>Courtney Henry</span>
                </div>
                <div>
                    <Button variant="outline-primary" className={globalClasses.outlinePrimaryBtn} style={{width:125}}>Reactivate</Button>
                    <Button variant="outline-primary" className={globalClasses.outlinePrimaryBtn} style={{width:125, marginLeft:10}}>Edit Details</Button>
                </div>
            </header>
            <Row>
                <Col lg={5}>
                    <img style={{height:170, borderRadius:7}} src={'https://www.biography.com/.image/t_share/MTc5OTk1MzU0Nzk4NzYxMDgy/gettyimages-858141682.jpg'} alt="profile"/>
                    <div className={classes.subHeadings}>Description</div>
                    <div style={{width:'85%'}}>
                        Aliqua id fugiat nostrud irure ex 
                        duis ea quis id quis ad et. Sunt qui 
                        esse pariatur duis deserunt mollit
                        dolore cillum minim tempor enim. 
                    </div>
                    <div className={classes.subHeadings}>Contact details</div>
                    <div>+91 12345 67890  |  <span style={{color:colors.nakshBlue}}>artistname@email.com</span></div>
                    <div className={classes.subHeadings}>Location</div>
                    <div>Hassan, KA</div>
                    <div className={classes.subHeadings}>Organization</div>
                    <div style={{color:colors.nakshBlue}}>ABCD Inc.</div>
                    <div className={classes.subHeadings}>Artform</div>
                    <div>Lorem Ipsum</div>
                    <div className={classes.subHeadings}>Social Media</div>
                    <div className={globalClasses.flexRowSpace} style={{width:'30%', marginTop:5}}>
                        <Globe/>
                        <Instagram/>
                        <Facebook/>
                    </div>
                </Col>
                <Col lg={7} style={{borderLeft:'1.5px solid rgba(0, 25, 76, 0.1)'}}>
                    <div style={{fontWeight:600, fontSize:20, marginBottom:10}}>Courtneys artwork</div>
                    <Row>
                        {/* {renderArtwork()} */}
                        {emptyState()}
                    </Row>
                </Col>
            </Row>
        </motion.div>
    )
}

