import React, { Component } from 'react';
import { Row, Col, Button, Form } from 'react-bootstrap';
import { motion } from "framer-motion";

import ArtistNftForm from '../../components/ArtistNftForm';
import Modal from '../../components/uiElements/Modal';
import { functions, animateConfigs } from '../../constants';
import { EditIcon, Profile, Upload } from '../../components/svgComponents';
import classes from './artist.module.css';
import globalClasses from '../../constants/globalStyles.module.css';

export default class CreateArtistNft extends Component {

    constructor(props) {
        super(props);
        this.state = {
            showModal: false,
            artistImage: null
        }
    }
      
    handleImage = async (e) => {
        const res = await functions.readImage(e);
        this.setState({artistImage:res[0]});
    }

    handleDeleteImage = () => {

    }

    buildImgTag = () => {
        return <div style={{display:'flex', marginRight:10}}>
            <img height='180px' width= '180px' style={{borderRadius:100}} src={this.state.artistImage} alt="upload"/>
            {/* <p onClick={this.handleDeleteImage()} style={{marginLeft: -13, marginTop: -10, marginRight:10}}><CancelIcon/></p> */}
        </div>
    } 

    handleMintNft = () => {
        this.props.history.push('/viewartistnft/1234')
    }
    

    render() {
        return (
            <motion.div variants={animateConfigs.fadeInOut} initial="hidden" animate="show">
                <div className={globalClasses.flexRowSpace}> 
                    <div style={{fontWeight:600, fontSize:22}}>New NFT</div>
                    <div>
                        <Button variant="outline-primary" style={{width:125}} className={globalClasses.outlinePrimaryBtn}>Cancel</Button>
                        <Button onClick={this.handleMintNft} style={{width:125, height:40, marginLeft:10, opacity:0.5}} className={globalClasses.primaryBtn}>Mint NFT</Button>
                    </div>
                </div>
                <div style={{marginTop:25, display:'flex', justifyContent:'space-between'}}>
                    <div className={globalClasses.containerCard} style={{width:'28%', textAlign:'center', padding: "50px 0px", height: "fit-content"}}>
                        <label htmlFor="addImg" style={{position:'relative', cursor:'pointer'}}>
                            <input onChange={(e) => this.handleImage(e)} id="addImg" hidden type="file" name="Pick an Image" accept="image/x-png,image/gif,image/jpeg"/>
                            {this.state.artistImage ?
                                this.buildImgTag() :
                            <Upload/>}
                        </label>
                        <div style={{fontSize:16, fontWeight:600, opacity:0.7, marginTop:15}}>Upload your NFT here</div>
                        <div style={{fontSize:14, fontWeight:600, opacity:0.5, marginTop:10}}>(Supports JPEG, .jpg, .png, .mp4 format)</div>
                    </div>
                    <div style={{width:'70%'}}>
                        <ArtistNftForm/>
                        <div style={{marginTop:15}} className={globalClasses.containerCard}>
                            <Button onClick={() => this.setState({showModal:!this.state.showModal})} variant="outline-primary" style={{width:'100%'}} className={globalClasses.outlinePrimaryBtn}>
                                Add custom fields
                            </Button>
                        </div>
                    </div>
                </div>
                <Modal 
                    show={this.state.showModal}
                    onHide={() => this.setState({showModal:false})}
                />
            </motion.div>
        )
    }
} 
